import React from "react";
import FoodItem from "../food_item/food_item";
import styles from "./food_list.module.css";

const FoodList = React.forwardRef(({ foods, onFoodClick }, ref) => {
  return (
    <section className={styles.foodBox}>
      {/* <div className={styles.sidebarLeft}>AD</div> */}
      <ul className={styles.foods}>
        {foods &&
          foods.map((food) => (
            <FoodItem
              key={food.id}
              food={food}
              onFoodClick={onFoodClick}
              ref={ref}
            />
          ))}
      </ul>
      {/* <div className={styles.sidebarRight}>AD</div> */}
    </section>
  );
});

export default FoodList;
